import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Result from './component/result';
import "./ResultPage.css";

import Ali from "./component/foto all MT/Ali.jpeg";
import Elsa from "./component/foto all MT/Elsa.jpeg";
import Sahala from "./component/foto all MT/Sahala.jpeg";

const nominatorsImg = [Ali, Elsa, Sahala];
const nominatorsName = ['Ali', 'Elsa', 'Sahala'];
const nominatorsFullName = ['Ali Utek', 'Elsa Srinita', 'Sahala Qaulan'];

const ResultPage = () => {

  const [nominators, setNominators] = useState(null);
  const [stateNom, setStateNom] = useState(null);
  const [totalVoter, setTotalVoter] = useState(0);

  const idx = (name) => {
    return nominatorsName.indexOf(name);
  }



  useEffect(() => {
    axios.get(`https://form-staff.cyclic.app/allNominators`)
      .then(res => {
       res.data.sort((a,b) => {
         return b.voter - a.voter
       });
       setNominators(res.data);
       console.log(res.data)

       let temp_totalVoter = 0;
       for(let i = 0 ; i < res.data.length ; i++) {
          temp_totalVoter += res.data[i].voter
       }
       setTotalVoter(temp_totalVoter)
      })
  }, [stateNom])

    if(!nominators) {
      return (<div className="component">loading data</div>)
    } else {
      console.log(totalVoter);
      return (
        <div className="component">
          <h2 className="result__title">Hasil Pemilihan<br/> Staff BA of The Month</h2>
          {nominators.map((nominator, index) => {
            return <Result name={nominatorsFullName[idx(nominators[index].name)]} src_img={nominatorsImg[idx(nominators[index].name)]} voter={nominators[index].voter} percentage={ Math.round(nominators[index].voter/totalVoter*100) } />
          })}
        </div>
     );
    }
}
  
export default ResultPage;

