          // <Result name={nominatorsFullName[idx(nominators[1].name)]} src_img={nominatorsImg[idx(nominators[1].name)]} voter={nominators[1].voter} percentage={ Math.round(nominators[1].voter/totalVoter*100) } />
          // <Result name={nominatorsFullName[idx(nominators[2].name)]} src_img={nominatorsImg[idx(nominators[2].name)]} voter={nominators[2].voter} percentage={ Math.round(nominators[2].voter/totalVoter*100) } />